<template>
	<div class="add-update-preview">
		<el-form
			class="add-update-form"
			ref="ruleForm"
			:model="ruleForm"
			:rules="rules"
			label-width="120px"
			>
			<el-form-item class="add-item" label="" prop="title">
				<el-input v-model="ruleForm.title" 
					placeholder="" clearable :disabled=" false  ||ro.title"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" v-if="type!='cross' || (type=='cross' && !ro.imgurl)" prop="imgurl">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="ruleForm.imgurl?ruleForm.imgurl:''"
					@change="imgurlUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item class="add-item" v-else label="" prop="imgurl">
				<img v-if="ruleForm.imgurl.substring(0,4)=='http'" class="upload-img" v-bind:key="index" :src="ruleForm.imgurl.split(',')[0]">
				<img v-else class="upload-img" v-bind:key="index" v-for="(item,index) in ruleForm.imgurl.split(',')" :src="baseUrl+item">
			</el-form-item>
			<el-form-item class="add-item" label="" prop="jiage">
				<el-input-number v-model="ruleForm.jiage" placeholder="" :disabled=" false ||ro.jiage"></el-input-number>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="procity">
				<el-input v-model="ruleForm.procity" 
					placeholder="" clearable :disabled=" false  ||ro.procity"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shopname">
				<el-input v-model="ruleForm.shopname" 
					placeholder="" clearable :disabled=" false  ||ro.shopname"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="salesnum">
				<el-input v-model.number="ruleForm.salesnum" 
					placeholder="" clearable :disabled=" false  ||ro.salesnum"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="salestext">
				<el-input v-model="ruleForm.salestext" 
					placeholder="" clearable :disabled=" false  ||ro.salestext"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="nickname">
				<el-input v-model="ruleForm.nickname" 
					placeholder="" clearable :disabled=" false  ||ro.nickname"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="shipingy">
				<el-input v-model="ruleForm.shipingy" 
					placeholder="" clearable :disabled=" false  ||ro.shipingy"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="baozhuanggg">
				<el-input v-model="ruleForm.baozhuanggg" 
					placeholder="" clearable :disabled=" false  ||ro.baozhuanggg"></el-input>
			</el-form-item>
			<el-form-item class="add-item" label="" prop="detailurl">
				<el-input
					type="textarea"
					:rows="8"
					placeholder=""
					v-model="ruleForm.detailurl">
					</el-input>
			</el-form-item>

			<el-form-item class="add-btn-item">
				<el-button class="submitBtn"  type="primary" @click="onSubmit">
					<span class="icon iconfont icon-kaitongfuwu"></span>
					<span class="text"></span>
				</el-button>
				<el-button class="closeBtn" @click="back()">
					<span class="icon iconfont icon-shanchu1"></span>
					<span class="text"></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				id: '',
				baseUrl: '',
				ro:{
					title : false,
					imgurl : false,
					jiage : false,
					procity : false,
					shopname : false,
					salesnum : false,
					salestext : false,
					nickname : false,
					shipingy : false,
					baozhuanggg : false,
					detailurl : false,
				},
				type: '',
				userTableName: localStorage.getItem('UserTableName'),
				ruleForm: {
					title: '',
					imgurl: '',
					jiage: '',
					procity: '',
					shopname: '',
					salesnum: '',
					salestext: '',
					nickname: '',
					shipingy: '',
					baozhuanggg: '',
					detailurl: '',
				},


				rules: {
					title: [
					],
					imgurl: [
					],
					jiage: [
						{ validator: this.$validate.isNumber, trigger: 'blur' },
					],
					procity: [
					],
					shopname: [
					],
					salesnum: [
						{ validator: this.$validate.isIntNumer, trigger: 'blur' },
					],
					salestext: [
					],
					nickname: [
					],
					shipingy: [
					],
					baozhuanggg: [
					],
					detailurl: [
						{ validator: this.$validate.isURL, trigger: 'blur' },
					],
				},
				centerType: false,
			};
		},
		computed: {



		},
		components: {
		},
		created() {
			if(this.$route.query.centerType){
				this.centerType = true
			}
			//this.bg();
			let type = this.$route.query.type ? this.$route.query.type : '';
			this.init(type);
			this.baseUrl = this.$config.baseUrl;
		},
		methods: {
			getMakeZero(s) {
				return s < 10 ? '0' + s : s;
			},
			// 
			download(file){
				window.open(`${file}`)
			},
			// 
			init(type) {
				this.type = type;
				if(type=='cross'){
					var obj = JSON.parse(localStorage.getItem('crossObj'));
					for (var o in obj){
						if(o=='title'){
							this.ruleForm.title = obj[o];
							this.ro.title = true;
							continue;
						}
						if(o=='imgurl'){
							this.ruleForm.imgurl = obj[o]?obj[o].split(",")[0]:'';
							this.ro.imgurl = true;
							continue;
						}
						if(o=='jiage'){
							this.ruleForm.jiage = obj[o];
							this.ro.jiage = true;
							continue;
						}
						if(o=='procity'){
							this.ruleForm.procity = obj[o];
							this.ro.procity = true;
							continue;
						}
						if(o=='shopname'){
							this.ruleForm.shopname = obj[o];
							this.ro.shopname = true;
							continue;
						}
						if(o=='salesnum'){
							this.ruleForm.salesnum = obj[o];
							this.ro.salesnum = true;
							continue;
						}
						if(o=='salestext'){
							this.ruleForm.salestext = obj[o];
							this.ro.salestext = true;
							continue;
						}
						if(o=='nickname'){
							this.ruleForm.nickname = obj[o];
							this.ro.nickname = true;
							continue;
						}
						if(o=='shipingy'){
							this.ruleForm.shipingy = obj[o];
							this.ro.shipingy = true;
							continue;
						}
						if(o=='baozhuanggg'){
							this.ruleForm.baozhuanggg = obj[o];
							this.ro.baozhuanggg = true;
							continue;
						}
						if(o=='detailurl'){
							this.ruleForm.detailurl = obj[o];
							this.ro.detailurl = true;
							continue;
						}
					}
				}else if(type=='edit'){
					this.info()
				}

				if (localStorage.getItem('raffleType') && localStorage.getItem('raffleType') != null) {
					localStorage.removeItem('raffleType')
					setTimeout(() => {
						this.onSubmit()
					}, 300)
				}
			},

			// 
			// 
			info() {
				this.$http.get(`xinjiangtechan/detail/${this.$route.query.id}`, {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.ruleForm = res.data.data;
					}
				});
			},
			// 
			async onSubmit() {
				if(!this.ruleForm.id) {
					delete this.ruleForm.userid
				}
				await this.$refs["ruleForm"].validate(async valid => {
					if(valid) {
						if(this.type=='cross'){
							var statusColumnName = localStorage.getItem('statusColumnName');
							var statusColumnValue = localStorage.getItem('statusColumnValue');
							if(statusColumnName && statusColumnName!='') {
								var obj = JSON.parse(localStorage.getItem('crossObj'));
								if(!statusColumnName.startsWith("[")) {
									for (var o in obj){
										if(o==statusColumnName){
											obj[o] = statusColumnValue;
										}
									}
									var table = localStorage.getItem('crossTable');
									await this.$http.post(table+'/update', obj).then(res => {});
								}
							}
						}


						await this.$http.post(`xinjiangtechan/${this.ruleForm.id?'update':this.centerType?'save':'add'}`, this.ruleForm).then(async res => {
							if (res.data.code == 0) {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.$router.go(-1);
										
									}
								});
							} else {
								this.$message({
									message: res.data.msg,
									type: 'error',
									duration: 1500
								});
							}
						});
					}
				});
			},
			// uuid
			getUUID () {
				return new Date().getTime();
			},
			// 
			back() {
				this.$router.go(-1);
			},
			imgurlUploadChange(fileUrls) {
				this.ruleForm.imgurl = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
		}
	};
</script>

